export class XeusInterpreter {
    constructor(xeus_raw_interpreter) {
        this.xeus_raw_interpreter = xeus_raw_interpreter;
    }
    registerPublisher(publisher) {
        this.xeus_raw_interpreter.register_publisher(publisher);
    }
    registerStdInSender(stdInSender) {
        this.xeus_raw_interpreter.register_stdin_sender(stdInSender);
    }
    async executeRequestAsync(code, silent, storeHistory, userExpressions, allowStdin) {
        let res = await this.xeus_raw_interpreter.execute_request(code, silent, storeHistory, JSON.stringify(userExpressions), allowStdin);
        return JSON.parse(res);
    }
    completeRequest(code, cursorPos) {
        let res = this.xeus_raw_interpreter.complete_request(code, cursorPos);
        return JSON.parse(res);
    }
    kernelInfoRequest(code, cursorPos) {
        let res = this.xeus_raw_interpreter.kernel_info_request();
        return JSON.parse(res);
    }
    get parentHeader() {
        return JSON.parse(this.xeus_raw_interpreter.get_parent_header_str());
    }
    set parentHeader(ph) {
        this.xeus_raw_interpreter.set_parent_header_str(JSON.stringify(ph));
    }
    set input(inputFunc) {
        this.xeus_raw_interpreter.set_async_input_func(inputFunc);
    }
}
